import * as c from '../constants';

const initialState = {
    usedQuestions: [],
    firstTeamName: c.FIRST_TEAM_NAME,
    secondTeamName: c.SECOND_TEAM_NAME,
    activeTeamRoundPoints: 0,
    firstTeamPoints: 0,
    secondTeamPoints: 0,
    gameRound: 1,
    activeTeam: 1,
    isHeaderTimerEnded: false,
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case c.RESET_POINTS:
            return {
                ...state,
                firstTeamPoints: 0,
                secondTeamPoints: 0,
                gameRound: 1,
                activeTeam: 1,
                usedQuestions: [],
                activeTeamRoundPoints: 0,
            };
        case c.TURN_ON_HEADER_TIMER_ENDED:
            return {
                ...state,
                isHeaderTimerEnded: true,
            };
        case c.TURN_OFF_HEADER_TIMER_ENDED:
            return {
                ...state,
                isHeaderTimerEnded: false,
            };
        case c.SET_ACTIVE_TEAM:
            return {
                ...state,
                activeTeam: state.activeTeam === 1 ? 2 : 1,
            };
        case c.INCREASE_GAME_ROUND:
            return {
                ...state,
                gameRound: state.gameRound + 1,
            };
        case c.PUT_QUESTION_TO_USED_ARRAY:
            return {
                ...state,
                usedQuestions: [...[action.payload], ...state.usedQuestions],
            };
        case c.DECREASE_ACTIVE_TEAM_POINTS:
            return {
                ...state,
                firstTeamPoints:
                    state.activeTeam === 1
                        ? state.firstTeamPoints - 1
                        : state.firstTeamPoints,
                secondTeamPoints:
                    state.activeTeam === 2
                        ? state.secondTeamPoints - 1
                        : state.secondTeamPoints,
            };
        case c.INCREASE_ACTIVE_TEAM_POINTS:
            return {
                ...state,
                firstTeamPoints:
                    state.activeTeam === 1
                        ? state.firstTeamPoints + state.activeTeamRoundPoints
                        : state.firstTeamPoints,
                secondTeamPoints:
                    state.activeTeam === 2
                        ? state.secondTeamPoints + state.activeTeamRoundPoints
                        : state.secondTeamPoints,
            };
        case c.INCREASE_NOT_ACTIVE_TEAM_POINTS:
            return {
                ...state,
                firstTeamPoints:
                    state.activeTeam !== 1
                        ? state.firstTeamPoints + 1
                        : state.firstTeamPoints,
                secondTeamPoints:
                    state.activeTeam !== 2
                        ? state.secondTeamPoints + 1
                        : state.secondTeamPoints,
            };
        case c.INCREASE_ACTIVE_TEAM_ROUND_POINTS:
            return {
                ...state,
                activeTeamRoundPoints: state.activeTeamRoundPoints + 1,
            };
        case c.DECREASE_ACTIVE_TEAM_ROUND_POINTS:
            return {
                ...state,
                activeTeamRoundPoints: state.activeTeamRoundPoints - 1,
            };
        case c.RESET_ACTIVE_TEAM_ROUND_POINTS:
            return {
                ...state,
                activeTeamRoundPoints: 0,
            };
        default:
            return state;
    }
};

export default reducer;
