import React, { useState } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import * as c from '../../constants';
import { headerTimerFunc } from '../../utils/header-timer-layout';
import {
    _headerTimerEnded,
    _headerTimerReset,
    _resetActiveTeamRoundPoints,
} from '../../actions';
import { HeaderTimerCounter } from '../../utils/header-timer-counter';
import { Routes } from '../app/app';

export const GameRoundHeaderWrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const CloseGameRoundButton = styled.div`
    width: 30px;
    height: 30px;
    position: relative;
    &:after {
        content: '';
        height: 30px;
        border-left: 2px solid #fff;
        position: absolute;
        transform: rotate(45deg);
        left: 50%;
    }
    &:before {
        content: '';
        height: 30px;
        border-left: 2px solid #fff;
        position: absolute;
        transform: rotate(-45deg);
        left: 50%;
    }
`;

const HeaderTimer = styled.p`
    padding-left: 11%;
    color: #fbe739;
    font-size: 7.4vh;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 1.8px;
    text-align: center;
`;

const GameRoundNumber = styled.p`
    font-size: 3vh;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: 0.7px;
    text-align: center;
    color: #ffffff;
`;

const GameRoundHeader = props => {
    const {
        gameRound,
        _headerTimerEnded,
        _headerTimerReset,
        _resetActiveTeamRoundPoints,
    } = props;
    const [secondsLeft, setSecondsLeft] = useState(c.SECONDS_FOR_ROUND);
    const headerTimer = headerTimerFunc(secondsLeft);

    HeaderTimerCounter(secondsLeft, setSecondsLeft);

    if (!secondsLeft) {
        _headerTimerEnded();
    }
    const handleClick = () => {
        _headerTimerReset();
        _resetActiveTeamRoundPoints();
    };

    return (
        <GameRoundHeaderWrapper>
            <Link to={Routes.MAIN} style={{ textDecoration: 'none' }}>
                <CloseGameRoundButton onClick={handleClick} />
            </Link>
            <HeaderTimer>{headerTimer}</HeaderTimer>
            <GameRoundNumber>
                Раунд
                <br />
                {gameRound}
            </GameRoundNumber>
        </GameRoundHeaderWrapper>
    );
};

GameRoundHeader.propTypes = {
    gameRound: PropTypes.number,
    _headerTimerEnded: PropTypes.func,
    _headerTimerReset: PropTypes.func,
    _resetActiveTeamRoundPoints: PropTypes.func,
};

const mapStateToProps = ({ gameRound }) => ({
    gameRound,
});

const mapDispatchToProps = {
    _headerTimerEnded,
    _headerTimerReset,
    _resetActiveTeamRoundPoints,
};

export default connect(mapStateToProps, mapDispatchToProps)(GameRoundHeader);
