export const checkForActiveTeamBackground = activeTeam => {
    if (activeTeam === 2) {
        return 'linear-gradient(to bottom, #290000 0%, #660101 96%)';
    } else {
        return 'linear-gradient(to bottom, #000e29, #011766 96%)';
    }
};

export const checkForActiveTeamName = (
    activeTeam,
    firstTeamName,
    secondTeamName,
) => {
    if (activeTeam === 2) {
        return secondTeamName;
    } else {
        return firstTeamName;
    }
};

export const checkForActiveTeamTextColor = activeTeam => {
    if (activeTeam === 2) {
        return '#f95721';
    } else {
        return '#81cfff';
    }
};
