import { createStore } from 'redux';
import throttle from 'lodash.throttle';

import reducer from './reducers';
import { loadState, saveState } from './utils/local-storage';

const persistedState = loadState();

const store = createStore(reducer, persistedState);

store.subscribe(
    throttle(() => {
        saveState({
            usedQuestions: store.getState().usedQuestions,
            firstTeamName: store.getState().firstTeamName,
            secondTeamName: store.getState().secondTeamName,
            firstTeamPoints: store.getState().firstTeamPoints,
            secondTeamPoints: store.getState().secondTeamPoints,
            gameRound: store.getState().gameRound,
            activeTeam: store.getState().activeTeam,
            activeTeamRoundPoints: store.getState().activeTeamRoundPoints,
        });
    }, 1000),
);

export default store;
