import React, { Component } from 'react';
import styled from 'styled-components';
import { GameNameText } from '../../UI/game-name-text';

const ErrorBoundaryWrapper = styled.div`
    position: absolute;
    background: linear-gradient(to bottom, #000e29, #011766 96%);
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
`;

export default class ErrorBoundary extends Component {
    state = {
        hasError: false,
    };

    componentDidCatch() {
        this.setState({ hasError: true });
    }

    render() {
        if (this.state.hasError) {
            return (
                <ErrorBoundaryWrapper>
                    <GameNameText width="80%" fontSize="4vh">
                        Проблемы с соединением, попробуй обновить страницу
                    </GameNameText>
                </ErrorBoundaryWrapper>
            );
        }
        return this.props.children;
    }
}
