import React from 'react';
import styled, { createGlobalStyle } from 'styled-components';
import { Route, Switch, HashRouter as Router } from 'react-router-dom';

import * as c from '../../constants';
import Dited from '../../font/Dited.otf';
import Montserrat from '../../font/Montserrat-Regular.ttf';
import MainScreen from '../main-screen';
import Logo from '../logo';
import GameRound from '../game-round';

export const GlobalStyle = createGlobalStyle`
        @font-face {
        font-family: 'Dited';
        src: url('${Dited}') format("opentype")
      };
        @font-face {
        font-family: 'Montserrat';
        src: url('${Montserrat}') format("opentype")
      };
`;

window.addEventListener('load', () => {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
});

window.addEventListener('resize', () => {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
});

const Main = styled.div`
    position: fixed;
    display: flex;
    justify-content: center;
    align-content: center;
    font-size: ${c.FONT_SIZE};
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    width: 100%;
    margin: 0 auto;
    background-color: #000000;
`;

export const Routes = {
    LOGO: '/',
    MAIN: '/main',
    GAMEROUND: '/gameround',
};

const App = () => {
    return (
        <Main>
            <Router>
                <Switch>
                    <Route exact path={Routes.LOGO} component={Logo} />
                    <Route path={Routes.MAIN} component={MainScreen} />
                    <Route path={Routes.GAMEROUND} component={GameRound} />
                </Switch>
            </Router>
        </Main>
    );
};

export default App;
