import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import TeamWindow from '../team-window';
import { _putQuestionToArray } from '../../actions';
import { QuestionService } from '../../services/questionService';
import { Routes } from '../app/app';
import { Button } from '../../UI/button';

const BoardBackground = styled.div`
    position: absolute;
    width: 93.75%;
    height: 48%;
    background-color: #0a1836;
    top: 22%;
    box-shadow: 0 2px 13px 0 #ffffff;
    border: solid 1px #ffffff;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    border-radius: 5px;

    @media (min-width: 768px) {
        max-width: 70%;
    }
`;

const StyledConfig = {
    linkWrapper: {
        textDecoration: 'none',
        width: '89%',
        position: 'relative',
        height: '20%',
    },
};

const TeamsBoard = props => {
    const { _putQuestionToArray } = props;
    const handleClick = () => {
        _putQuestionToArray(QuestionService.randomQuestion(0));
    };
    return (
        <BoardBackground>
            <TeamWindow />
            <Link
                to={Routes.GAMEROUND}
                style={{
                    ...StyledConfig.linkWrapper,
                }}
            >
                <Button height="8vh" onClick={handleClick}>
                    Играть
                </Button>
            </Link>
        </BoardBackground>
    );
};

TeamsBoard.propTypes = {
    _putQuestionToArray: PropTypes.func,
};

const mapDispatchToProps = {
    _putQuestionToArray,
};

export default withRouter(connect(null, mapDispatchToProps)(TeamsBoard));
