import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import logoImg from '../logo/logo.png';
import TeamsBoard from '../teams-board';
import { _resetPoints } from '../../actions';
import { GameNameText } from '../../UI/game-name-text';
import { Button } from '../../UI/button';

export const MainWrapper = styled.div`
    position: relative;
    background-image: url(${logoImg});
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: 95%;
    width: 100%;
    display: flex;
    justify-content: center;
`;

const StyledConfig = {
    resetButton: {
        width: '65%',
        height: '8%',
        top: '86%',
        bgcImg: 'linear-gradient(to bottom, #ffffff, #88d6ff 25%, #83b9ff)',
    },
};

const MainScreen = props => {
    const handleClick = () => {
        const { _resetPoints } = props;
        _resetPoints();
    };

    return (
        <MainWrapper>
            <GameNameText fontSize="7vh" top="12%">
                Скажи иначе
            </GameNameText>
            <TeamsBoard />
            <Button {...StyledConfig.resetButton} onClick={handleClick}>
                Сброс
            </Button>
        </MainWrapper>
    );
};

MainScreen.propTypes = {
    _resetPoints: PropTypes.func,
};

const mapDispatchToProps = {
    _resetPoints,
};

export default connect(null, mapDispatchToProps)(MainScreen);
