import { _arrayOfQuestionsEasy } from './arrayOfQuestionsEasy';
import { _arrayOfQuestionsMedium } from './arrayOfQuestionsMedium';
import { _arrayOfQuestionsHard } from './arrayOfQuestionsHard';

class QuestionService {
    arrayOfQuestionsForUseEasy = [..._arrayOfQuestionsEasy];
    arrayOfQuestionsForUseMedium = [..._arrayOfQuestionsMedium];
    arrayOfQuestionsForUseHard = [..._arrayOfQuestionsHard];

    resetArrayQuestionForUseEasy = () => {
        this.arrayOfQuestionsForUseEasy = [..._arrayOfQuestionsEasy];
    };
    resetArrayQuestionForUseMedium = () => {
        this.arrayOfQuestionsForUseMedium = [..._arrayOfQuestionsMedium];
    };
    resetArrayQuestionForUseHard = () => {
        this.arrayOfQuestionsForUseHard = [..._arrayOfQuestionsHard];
    };

    randomQuestion = activeTeamRoundPoints => {
        if (activeTeamRoundPoints < 4) {
            const randomQuestion = this.arrayOfQuestionsForUseEasy[
                Math.floor(
                    Math.random() * this.arrayOfQuestionsForUseEasy.length,
                )
            ];
            const indexOfRandomQuestion = this.arrayOfQuestionsForUseEasy.indexOf(
                randomQuestion,
            );
            this.arrayOfQuestionsForUseEasy.splice(indexOfRandomQuestion, 1);
            if (this.arrayOfQuestionsForUseEasy.length === 0) {
                this.resetArrayQuestionForUseEasy();
            }
            return randomQuestion;
        } else if (activeTeamRoundPoints > 3 && activeTeamRoundPoints < 7) {
            const randomQuestion = this.arrayOfQuestionsForUseMedium[
                Math.floor(
                    Math.random() * this.arrayOfQuestionsForUseMedium.length,
                )
            ];
            const indexOfRandomQuestion = this.arrayOfQuestionsForUseMedium.indexOf(
                randomQuestion,
            );
            this.arrayOfQuestionsForUseMedium.splice(indexOfRandomQuestion, 1);
            if (this.arrayOfQuestionsForUseMedium.length === 0) {
                this.resetArrayQuestionForUseMedium();
            }
            return randomQuestion;
        } else {
            const randomQuestion = this.arrayOfQuestionsForUseHard[
                Math.floor(
                    Math.random() * this.arrayOfQuestionsForUseHard.length,
                )
            ];
            const indexOfRandomQuestion = this.arrayOfQuestionsForUseHard.indexOf(
                randomQuestion,
            );
            this.arrayOfQuestionsForUseHard.splice(indexOfRandomQuestion, 1);
            if (this.arrayOfQuestionsForUseHard.length === 0) {
                this.resetArrayQuestionForUseHard();
            }
            return randomQuestion;
        }
    };
}
const instance = new QuestionService();
export { instance as QuestionService };
