import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import {
    checkForActiveTeamBackground,
    checkForActiveTeamName,
    checkForActiveTeamTextColor,
} from '../../utils/check-for-active-team';
import PropTypes from 'prop-types';

const TeamWindowRoundWrapper = styled.div`
    width: 42%;
    height: 27%;
    opacity: 0.82;
    background-image: ${props => props.bgImage};
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const TeamWindowRoundInfo = styled.p`
    font-size: ${props => props.fontSize};
    letter-spacing: 0.77px;
    text-align: center;
    color: ${props => props.color};
`;

const TeamWindowRound = props => {
    const {
        activeTeam,
        firstTeamName,
        secondTeamName,
        activeTeamRoundPoints,
    } = props;
    const activeTeamBackground = checkForActiveTeamBackground(activeTeam);
    const activeTeamTextColor = checkForActiveTeamTextColor(activeTeam);
    const activeTeamName = checkForActiveTeamName(
        activeTeam,
        firstTeamName,
        secondTeamName,
    );
    return (
        <TeamWindowRoundWrapper bgImage={activeTeamBackground}>
            <TeamWindowRoundInfo color={activeTeamTextColor} fontSize="4vh">
                {activeTeamName}
            </TeamWindowRoundInfo>
            <TeamWindowRoundInfo color={activeTeamTextColor} fontSize="12vh">
                {activeTeamRoundPoints}
            </TeamWindowRoundInfo>
        </TeamWindowRoundWrapper>
    );
};

TeamWindowRound.propTypes = {
    firstTeamPoints: PropTypes.number,
    secondTeamPoints: PropTypes.number,
    firstTeamName: PropTypes.string,
    secondTeamName: PropTypes.string,
    activeTeam: PropTypes.number,
};

const mapStateToProps = ({
    firstTeamPoints,
    secondTeamPoints,
    firstTeamName,
    secondTeamName,
    activeTeam,
    activeTeamRoundPoints,
}) => ({
    firstTeamPoints,
    secondTeamPoints,
    firstTeamName,
    secondTeamName,
    activeTeam,
    activeTeamRoundPoints,
});

export default connect(mapStateToProps)(TeamWindowRound);
