import styled from 'styled-components';
import React from 'react';
import './style.css';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

export const TeamWindowWrapper = styled.div`
    margin-top: 8.6%;
    width: 35.33%;
    height: 52.51%;
    background-color: ${props => props.bgColor || 'rgba(0, 146, 255, 0.2)'};
    font-size: 2vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`;

const TeamInfoWrapper = styled.p`
    font-size: ${props => props.fontSize || '4vh'};
    letter-spacing: ${props => props.letterSpacing || 0};
    color: ${props => props.color || '#ffffff'};
`;

export const RoundText = styled.p`
    padding-top: ${props => props.paddingTop};
    color: #ffffff;
    font-size: ${props => props.fontSize};
    text-align: center;
`;

export const RoundTextWrapper = styled.div`
    margin: 8.6% 3% 0 3%;
    height: 52.51%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const TeamWindow = props => {
    const {
        firstTeamPoints,
        secondTeamPoints,
        gameRound,
        firstTeamName,
        secondTeamName,
        activeTeam,
    } = props;
    return (
        <>
            <TeamWindowWrapper
                className={
                    'active-team ' + (activeTeam === 1 && 'active-team-1')
                }
            >
                <TeamInfoWrapper>{firstTeamName}</TeamInfoWrapper>
                <TeamInfoWrapper
                    fontSize={activeTeam === 1 ? '12vh' : '11vh'}
                    color="#45b6fc"
                    className={activeTeam === 1 && 'active-team-1-points'}
                >
                    {firstTeamPoints}
                </TeamInfoWrapper>
            </TeamWindowWrapper>

            <RoundTextWrapper>
                <RoundText fontSize="3vh">Раунд</RoundText>
                <RoundText fontSize="6vh">{gameRound}</RoundText>
            </RoundTextWrapper>

            <TeamWindowWrapper
                bgColor="rgba(227, 6, 17, 0.3)"
                className={
                    'active-team ' + (activeTeam === 2 && 'active-team-2')
                }
            >
                <TeamInfoWrapper>{secondTeamName}</TeamInfoWrapper>
                <TeamInfoWrapper
                    fontSize={activeTeam === 2 ? '12vh' : '11vh'}
                    color="#f95721"
                    className={activeTeam === 2 && 'active-team-2-points'}
                >
                    {secondTeamPoints}
                </TeamInfoWrapper>
            </TeamWindowWrapper>
        </>
    );
};

TeamWindow.propTypes = {
    firstTeamPoints: PropTypes.number,
    secondTeamPoints: PropTypes.number,
    gameRound: PropTypes.number,
    firstTeamName: PropTypes.string,
    secondTeamName: PropTypes.string,
    activeTeam: PropTypes.number,
};

const mapStateToProps = ({
    firstTeamPoints,
    secondTeamPoints,
    gameRound,
    firstTeamName,
    secondTeamName,
    activeTeam,
}) => ({
    firstTeamPoints,
    secondTeamPoints,
    gameRound,
    firstTeamName,
    secondTeamName,
    activeTeam,
});

export default connect(mapStateToProps)(TeamWindow);
