import React from 'react';
import styled, { keyframes } from 'styled-components';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

const dissapear = keyframes`
    from { 
        opacity: 0;
        filter: blur(1px);
    }
    to {
        opacity: 1;
        filter: blur(20px);
    }
`;

const GuessTextPanelWrapper = styled.div`
    width: 100%;
    height: 32%;
    box-shadow: 0 2px 13px 0 #ffffff;
    background-color: #ededed;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
`;

const GuessText = styled.p`
    color: #141738;
    padding: 0 5%;
    font-size: 5.3vh;
    text-align: center;
    animation-name: ${dissapear};
    animation-duration: 0.1s;
    font-family: 'Montserrat';
`;

const GuessTextPanel = ({ usedQuestions }) => {
    const question = usedQuestions[0].text;
    return (
        <GuessTextPanelWrapper>
            <GuessText key={usedQuestions[0].id}>{question}</GuessText>
        </GuessTextPanelWrapper>
    );
};

GuessTextPanel.propTypes = {
    usedQuestions: PropTypes.array,
};

const mapStateToProps = ({ usedQuestions }) => ({
    usedQuestions,
});

export default connect(mapStateToProps)(GuessTextPanel);
