import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

import logoImg from './logo.png';
import { _resetPoints } from '../../actions';
import { connect } from 'react-redux';
import { Routes } from '../app/app';
import { GameNameText } from '../../UI/game-name-text';
import { Button } from '../../UI/button';

export const LogoWrapper = styled.div`
    background-image: url(${logoImg});
    background-repeat: no-repeat;
    background-position-y: 50%;
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: center;
`;

const StyledConfig = {
    linkWrapper: {
        textDecoration: 'none',
        width: '84%',
        top: '78.5%',
        position: 'relative',
        height: '9%',
    },
};

const Logo = props => {
    const { _resetPoints } = props;
    const handleClick = () => {
        _resetPoints();
    };
    return (
        <LogoWrapper>
            <GameNameText fontSize="10vh" top="25%">
                Скажи
                <br />
                иначе
            </GameNameText>
            <Link
                to={Routes.MAIN}
                style={{
                    ...StyledConfig.linkWrapper,
                }}
            >
                <Button fontSize="7vh" type="button" onClick={handleClick}>
                    Старт
                </Button>
            </Link>
        </LogoWrapper>
    );
};

const mapDispatchToProps = {
    _resetPoints,
};

export default connect(null, mapDispatchToProps)(Logo);
