export const headerTimerFunc = secondsLeft => {
    const minutes = Math.floor(secondsLeft / 60);
    const seconds = secondsLeft % 60;
    const minLeft = minutes.toString().length === 1 ? '0' + minutes : minutes;
    const secLeft = seconds.toString().length === 1 ? '0' + seconds : seconds;
    return `${minLeft}:${secLeft}`;
};
