import { createReduxAction } from '../utils/create-redux-actions';
import * as c from '../constants';

export const _resetPoints = () => createReduxAction(c.RESET_POINTS);
export const _headerTimerEnded = () =>
    createReduxAction(c.TURN_ON_HEADER_TIMER_ENDED);
export const _headerTimerReset = () =>
    createReduxAction(c.TURN_OFF_HEADER_TIMER_ENDED);
export const _setActiveTeam = () => createReduxAction(c.SET_ACTIVE_TEAM);
export const _increaseGameRound = () =>
    createReduxAction(c.INCREASE_GAME_ROUND);
export const _putQuestionToArray = obj =>
    createReduxAction(c.PUT_QUESTION_TO_USED_ARRAY, obj);
export const _increaseActiveTeamPoints = () =>
    createReduxAction(c.INCREASE_ACTIVE_TEAM_POINTS);
export const _increaseNotActiveTeamPoints = () =>
    createReduxAction(c.INCREASE_NOT_ACTIVE_TEAM_POINTS);
export const _increaseActiveTeamRoundPoints = () =>
    createReduxAction(c.INCREASE_ACTIVE_TEAM_ROUND_POINTS);
export const _decreaseActiveTeamRoundPoints = () =>
    createReduxAction(c.DECREASE_ACTIVE_TEAM_ROUND_POINTS);
export const _resetActiveTeamRoundPoints = () =>
    createReduxAction(c.RESET_ACTIVE_TEAM_ROUND_POINTS);
