import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import { QuestionService } from '../../services/questionService';
import { Routes } from '../app/app';
import { Button } from '../../UI/button';
import {
    _increaseGameRound,
    _headerTimerReset,
    _putQuestionToArray,
    _setActiveTeam,
    _increaseActiveTeamPoints,
    _increaseNotActiveTeamPoints,
    _increaseActiveTeamRoundPoints,
    _decreaseActiveTeamRoundPoints,
    _resetActiveTeamRoundPoints,
} from '../../actions';
import { changeColorLeftButton } from '../../utils/change-color-left-button';

const GameRoundButtonsWrapper = styled.div`
    width: 100%;
    display: flex;
`;

const StyleConfig = {
    leftButton: {
        height: '9vh',
        fontSize: '4vh',
        width: '47%',
        bgcImg: 'linear-gradient(to bottom, #ffffff, #88d6ff 25%, #83b9ff)',
    },
    rightButton: {
        height: '9vh',
        fontSize: '4vh',
        width: '47%',
    },
};

const GameRoundButtons = props => {
    const {
        activeTeam,
        isHeaderTimerEnded,
        history,
        _increaseGameRound,
        _headerTimerReset,
        _putQuestionToArray,
        _setActiveTeam,
        _increaseActiveTeamPoints,
        _increaseNotActiveTeamPoints,
        _increaseActiveTeamRoundPoints,
        _decreaseActiveTeamRoundPoints,
        _resetActiveTeamRoundPoints,
        activeTeamRoundPoints,
    } = props;

    const increasePointsActiveTeam = () => {
        _increaseActiveTeamRoundPoints();
        _putQuestionToArray(
            QuestionService.randomQuestion(activeTeamRoundPoints + 1),
        );

        if (isHeaderTimerEnded) {
            if (activeTeam === 2) {
                _increaseGameRound();
            }
            _increaseActiveTeamPoints();
            _resetActiveTeamRoundPoints();
            _setActiveTeam();
            history.push(Routes.MAIN);
            _headerTimerReset();
        }
    };

    const decreasePointsActiveTeam = () => {
        if (activeTeamRoundPoints) {
            _decreaseActiveTeamRoundPoints();
        }
        _putQuestionToArray(
            QuestionService.randomQuestion(activeTeamRoundPoints - 1),
        );
    };

    const increasePointsNotActiveTeam = () => {
        _increaseNotActiveTeamPoints();

        if (activeTeamRoundPoints) {
            _decreaseActiveTeamRoundPoints();
        }

        if (activeTeam === 2) {
            _increaseGameRound();
        }

        _increaseActiveTeamPoints();
        _resetActiveTeamRoundPoints();
        _setActiveTeam();
        history.push(Routes.MAIN);
        _headerTimerReset();
    };

    return (
        <GameRoundButtonsWrapper>
            <Button
                {...StyleConfig.leftButton}
                bgcImg={changeColorLeftButton(isHeaderTimerEnded)}
                onClick={
                    isHeaderTimerEnded
                        ? increasePointsNotActiveTeam
                        : decreasePointsActiveTeam
                }
            >
                {isHeaderTimerEnded ? '+1 сопернику' : 'пропуск'}
            </Button>
            <Button
                {...StyleConfig.rightButton}
                onClick={increasePointsActiveTeam}
            >
                +1
            </Button>
        </GameRoundButtonsWrapper>
    );
};

GameRoundButtons.propTypes = {
    firstTeamPoints: PropTypes.number,
    secondTeamPoints: PropTypes.number,
    activeTeam: PropTypes.number,
    isHeaderTimerEnded: PropTypes.bool,
    selectActiveTeamFirst: PropTypes.func,
    selectActiveTeamSecond: PropTypes.func,
    _increaseGameRound: PropTypes.func,
    _headerTimerReset: PropTypes.func,
    _putQuestionToArray: PropTypes.func,
    _decreaseActiveTeamPoints: PropTypes.func,
    _increaseActiveTeamPoints: PropTypes.func,
    _increaseNotActiveTeamPoints: PropTypes.func,
    _increaseActiveTeamRoundPoints: PropTypes.func,
    _resetActiveTeamRoundPoints: PropTypes.func,
};

const mapStateToProps = ({
    firstTeamPoints,
    secondTeamPoints,
    activeTeam,
    isHeaderTimerEnded,
    activeTeamRoundPoints,
}) => ({
    firstTeamPoints,
    secondTeamPoints,
    activeTeam,
    isHeaderTimerEnded,
    activeTeamRoundPoints,
});

const mapDispatchToProps = {
    _increaseGameRound,
    _headerTimerReset,
    _putQuestionToArray,
    _setActiveTeam,
    _increaseActiveTeamPoints,
    _increaseNotActiveTeamPoints,
    _increaseActiveTeamRoundPoints,
    _decreaseActiveTeamRoundPoints,
    _resetActiveTeamRoundPoints,
};

export default withRouter(
    connect(mapStateToProps, mapDispatchToProps)(GameRoundButtons),
);
